import React, {useState, useEffect} from "react";
import styled, {css} from "styled-components";
import axios from "axios";
import ReservePos from "../assets/reserve_pos.png";
import ReserveIm from "../assets/reserve_im.png";
import BookReservationModal from "../components/BookReservationModal";

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  margin-left: 30px;
  height: 70px;
  cursor: pointer;
`;

const Reserve = styled.img`
  margin-bottom: -10px;
`;

const Text = styled.p`
  padding: 0;
  font-size: 13px;
  font-family: Inter;
  font-weight: 800;

  ${(props) =>
          props.reservationStatus === " " &&
          css`
            color: #27ff00;
          `}

  ${props =>
          (props.reservationStatus === " " ||
                  props.reservationStatus === " " ||
                  props.reservationStatus === " ") &&
          css`
            color: #ff0000;
          `}
`;

const ReserveButton = ({bookId, userId}) => {
        const [reservationStatus, setReservationStatus] = useState("");
        const [isModalOpen, setIsModalOpen] = useState(false);

        useEffect(() => {
            //     
            axios.get(`http://localhost:8080/book/reserve?bookId=${bookId}&userId=${userId}`)
                .then((response) => {
                    const status = response.data;
                    setReservationStatus(status); //   
                })
                .catch((error) => {
                    console.error(error);
                });
        }, [bookId, userId]);

        const handleButtonClick = () => {
            if (reservationStatus === " ") { //     
                setIsModalOpen(true);
            } else if (reservationStatus === " ") { //    
                alert("   ");
            } else if (reservationStatus === " ") { //       
                alert("    .");
            } else { //        
                alert("   .  .")
            }
        };


        const closeModal = () => {
            setIsModalOpen(false);
        };

        let imageSource;
        let buttonText;

        if (reservationStatus === " ") {
            imageSource = ReservePos;
            buttonText = " ";
        } else if (reservationStatus === " ") {
            imageSource = ReserveIm;
            buttonText = "";
        } else if (reservationStatus === " ") {
            imageSource = ReserveIm;
            buttonText = "";
        } else {
            imageSource = ReserveIm;
            buttonText = "";
        }

        return (
            <>
                <Wrapper onClick={handleButtonClick}>
                    <Reserve src={imageSource}/>
                    <Text reservationStatus={reservationStatus}>{buttonText}</Text>
                </Wrapper>
                <BookReservationModal
                    isOpen={isModalOpen}
                    onClose={closeModal}
                />
            </>
        );
    }
;

export default ReserveButton;
