import React, { useState } from "react";
import styled from "styled-components";
import { Link } from 'react-router-dom';

const BookWrapper = styled.div`
  position: relative;
  width: 180px; /*     */
  height: 270px;
  margin-right: -20px;
  transition: transform 0.3s ease; /*     */

  &:hover {
    transform: scale(1.05); /*     */
  }
`;

const BookImage = styled.img`
  width: 100%;
  height: 250px;
  border-radius: 5px;
  box-shadow: 0px 7px 4px rgba(0, 0, 0, 0.2);
`;

const BookTitle = styled.p`
  color: black;
  font-size: 15px;
  font-weight: 600;
  margin-top: 5px;
  margin-left: 2px;
  text-decoration: none;
`;
const BookRezDate = styled.p`
  color: red;
  font-size: 15px;
  font-weight: 600;
`;

const CheckboxContainer = styled.label`
  position: absolute;
  top: 50px;
  left: 10px;
  z-index: 1;
  display: inline-block;
  background: #fff;
  padding: 5px;
  border-radius: 50%;
`;

const ReserveBookFrame = ({ book, selection, selected }) => {
    const [isChecked, setIsChecked] = useState(selected);

    const handleCheckboxChange = () => {
        setIsChecked(!isChecked);
        selection(book.bookId, !isChecked);
    };

    const handleCancelReservation = () => {
        if(isChecked) {
            const dataToSend = {
                bookId: book.bookId,
            };
        }
    };

    return (
    <div style = {{ position: "relative" }}>
        <Link to={`/book-detail/${book.bookId}`}> {/*     URL  */}
            <BookWrapper>
                <BookImage src={book.bookImage} alt={book.bookTitle} />
                <BookTitle>{book.bookTitle}</BookTitle>
                <BookRezDate>  : {book.bookRezDate}</BookRezDate>
            </BookWrapper>
        </Link>

     </div>
    );
};

export default ReserveBookFrame;