import React, {useState, useRef} from "react";
import styled from "styled-components";
import {useParams} from "react-router-dom";
import moment from "moment/moment";
import axios from "axios";
import {darken} from "polished";
import MyDatePicker from "./MyDatePicker";

const ModalOverlay = styled.div`
  margin-top: 30px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  color: black;
`;

const ModalContent = styled.div`
  width: 600px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  position: relative;
`;

const ModalHeader = styled.div`
  background: #EFF2FF;
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  justify-content: space-between; /*     */
`;
const CloseButton = styled.button`
  border: none;
  cursor: pointer;
  margin-right: 5px;
  background: none;
`;
const ModalTitle = styled.p`
  font-size: 17px;
  padding-left: 15px;
  font-weight: 700;
`;
const ReservationBtn = styled.button`
  justify-content: center;
  background: #A5B3FF;
  color: #FFF;
  border: none;
  border-radius: 5px;
  width: 80%;
  height: 40px;
  margin-bottom: 10px;

  &:hover {
    background: ${(props) =>
    props.backgroundColor
        ? darken(0.1, props.backgroundColor) //    
        : "#123456"};
  }
`;
const ContentArea = styled.div`
  margin: 5px 20px;
`
const Textarea = styled.p`
  font-size: 13.5px;
  color: #696969;
  font-weight: 600;
  margin: 40px 5px 20px 5px;
`;
const StyledText = styled.p`
  font-size: 14px;
  color: #1e1e1e;
  font-weight: bold;
  margin-bottom: 5px;
  margin-left: 5px;
`

function BookReservationModal({ isOpen, onClose }) {
    const [reservationStatus, setReservationStatus] = useState("");
    const [selectedDate, setSelectedDate] = useState(null);
    const { bookId } = useParams();

    if (!isOpen) return null;

    const handleReserveConfirm = () => {
        if (selectedDate) {
            const reserveDate = moment(selectedDate).format("YYYY-MM-DD");

            axios
                .post("http://localhost:8080/book/reserve", {
                    bookId: bookId,
                    bookRezDate: reserveDate,
                })
                .then((response) => {
                    const reserveStatus = response.data;
                    if (reserveStatus === " ") {
                        alert(" ");
                        setReservationStatus(" "); //     
                        onClose();
                    } else if (reserveStatus === " ") {
                        alert("   .");
                    } else {
                        alert("  .   ");
                    }
                })
                .catch((error) => {
                    console.error(error);
                    alert("   .");
                });
        } else {
            alert("  .");
        }
    };

    return (
        <ModalOverlay onClick={(e) => e.stopPropagation}>
            <ModalContent>
                <ModalHeader>
                    <ModalTitle>  </ModalTitle>
                    <CloseButton onClick={onClose}></CloseButton>
                </ModalHeader>
                <ContentArea>
                    <StyledText> </StyledText>
                    <MyDatePicker onDateChange={(date) => setSelectedDate(date)}/>
                    <Textarea>  ?<br/>
                             , ,    .<br/>
                              .</Textarea>
                </ContentArea>
                <ReservationBtn onClick={handleReserveConfirm()}></ReservationBtn>
            </ModalContent>
        </ModalOverlay>
    );
};


export default BookReservationModal;
